





















































































































.radio-wrapper{
    background-color: #EFEFF4;
    border-radius: 50px;
    color: #303030;
    letter-spacing: 0;
    text-align: center;
    border: 1px solid #EFEFF4;
    position: relative;
    display: inline-block;
    padding: 4px 8px;
    margin-bottom: 8px;
    input[type] {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0;
    }
    &-checked{
        background-color: #ffffff;
        border: 1px solid #008CD7;
        color: #008CD7;
    }
}



